  <template>
      <div class="whoask">
        <div class="container">
          <div class="character-display">
            <img :src="currentCharacter.imageUrl" :alt="currentCharacter.name">
          </div>
          <div class="character-info">
            <p>từ từ update sau...</p>
            <div class="character-list-container" ref="characterListContainer" @mousedown="startDrag" @mousemove="drag"
              @mouseup="endDrag" @mouseleave="endDrag">
              <div class="character-list" ref="characterList" :style="{ transform: `translateX(${translateX}px)` }">
                <img v-for="char in characters" :key="char.id" :src="char.imageUrl" :alt="char.name"
                  @click="loadCharacterDetails(char)" draggable="false" />
              </div>
              <button class="slider-btn prev" @click="prevSlide">[L]</button>
              <button class="slider-btn next" @click="nextSlide">[R]</button>
            </div>
            <div class="character-details">
              <h2>{{ currentCharacter.name }}</h2>
              <p>{{ currentCharacter.info }}</p>
            </div>
          </div>
        </div>
      </div>
  </template>

<script>
import charactersData from '@/assets/database.json';

export default {
  data() {
    return {
      characters: [],
      currentCharacter: {},
      currentIndex: 0,
      isDragging: false,
      startX: 0,
      translateX: 0,
      initialTranslateX: 0,
    };
  },
  mounted() {
    this.characters = charactersData;
    this.loadCharacterDetails(this.characters[0]);
  },
  methods: {
    loadCharacterDetails(character) {
      this.currentCharacter = character;
    },
    updateSliderPosition() {
      const offset = -this.currentIndex * 90;
      this.translateX = offset;
    },
    prevSlide() {
      if (this.currentIndex > 0) {
        this.currentIndex--;
        this.updateSliderPosition();
      }
    },
    nextSlide() {
      if (this.currentIndex < this.characters.length - 1) {
        this.currentIndex++;
        this.updateSliderPosition();
      }
    },
    startDrag(e) {
      this.isDragging = true;
      this.startX = e.pageX;
      this.initialTranslateX = this.translateX;
    },
    drag(e) {
      if (!this.isDragging) return;
      const moveX = e.pageX - this.startX;
      this.translateX = this.initialTranslateX + moveX;


    },
    endDrag() {
      if (this.isDragging) {
        this.isDragging = false;
        const listWidth = this.characters.length * 90;
        const containerWidth = this.$refs.characterListContainer.offsetWidth;
        let currentPosition = this.translateX;
        if (currentPosition > 0) {
          currentPosition = 0;
        }
        if (currentPosition < containerWidth - listWidth) {
          currentPosition = containerWidth - listWidth;
        }
        this.translateX = currentPosition;
        let newIndex = Math.round(-currentPosition / 90);

        if (newIndex > 0 && newIndex < this.characters.length - 1) {
          this.currentIndex = newIndex;
        }
        else if (newIndex < 0) {
          this.currentIndex = 0;
        }
        else if (newIndex > this.characters.length - 1) {
          this.currentIndex = this.characters.length - 1
        }
      }

    },
  },
};
</script>

